<script>
import {
  Hooper,
  Slide,
  Pagination as HooperPagination,
  // Navigation as HooperNavigation,
} from "hooper";
import "hooper/dist/hooper.css";

export default {
  name: "my-component",
  components: {
    Hooper,
    Slide,
    HooperPagination,
    // HooperNavigation,
  },
  data: () => ({
    guestHouseDescription:
      "〇〇にある〇〇のゲストハウス\r\n世界一周を経験したオーナーのあったらいいなを詰め込んだゲストハウスです。説明文 〜〜〜〜〜〜〜〜〜",

    items: [
      {
        src: require("@/assets/image/guesthouse2.png"),
      },
      {
        src: require("@/assets/image/guesthouse1.png"),
      },
      {
        src: "https://cdn.vuetifyjs.com/images/carousel/bird.jpg",
      },
      {
        src: "https://cdn.vuetifyjs.com/images/carousel/planet.jpg",
      },
    ],
    hooperSettings: {
      itemsToShow: 1,
      infiniteScroll: true,
      centerMode: true,
      wheelControl: false,
    },

    targetPos: null,
    scrollheight: null,
  }),

  mounted() {
    this.scrollheight = window.scrollY;
    // + window.scrollYがない場合、読み込み時のスクロール状態によりずれが発生する
    this.targetPos =
      this.$refs.room.getBoundingClientRect().top + this.scrollheight;
    window.addEventListener("scroll", this.handleScroll);
    alert(this.targetPos);
  },
  // destroyed() {
  //   window.removeEventListener("scroll", this.handleScroll);
  // },
  watch: {
    scrollheight: function () {
      // データが変化した時に行いたい処理this.targetPos =
      this.$refs.room.getBoundingClientRect().top + this.scrollheight;
      alert(this.targetPos);
    },
  },
  computed() {},
  methods: {
    async handleScroll() {
      //スクロール量 > ターゲット要素の位置
      if (window.scrollY > this.targetPos) {
        this.$refs.rightlist.classList.remove("hidden"); //hiddenクラスを削除
        this.$refs.rightlist.classList.add("visible"); //rightlistクラスを追加
      } else {
        this.$refs.rightlist.classList.add("hiddenout"); //hiddenクラスを追加
        this.$refs.rightlist.classList.remove("visible"); //rightlistクラスを削除
      }
    },
  },
};
</script>

<style scoped>
.hidden {
  visibility: hidden;
}
.hiddenout {
